@import "./utils/variables";
@import "./utils/mixins";
@import "./utils/extends";
@import "./fonts";
@import "./icons";

.page {
  font-family: Mont, sans-serif;
  font-size: 14px;
  line-height: 21px;
  font-weight: 500;
  color: $primary-color;

  scroll-behavior: smooth;

  &__body {
    margin: 0;
    min-width: $min-width;
  }

  &__wraper {
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    justify-content: space-between;

    >main {
      flex: 1 1 auto;
    }
  }
}


@import "./blocks/header";
