%centered-flexbox {
  display: flex;
  justify-content: center;
  align-items: center;
}

%h1 {
  font-weight: 700;
  font-size: 32px;
  line-height: 41px;
  letter-spacing: -0.01em;
}

%h2 {
  font-weight: 700;
  font-size: 22px;
  line-height: 31px;
}

%h3 {
  font-weight: 600;
  font-size: 20px;
  line-height: 26px;
}

%small-text {
  font-size: 12px;
  line-height: 15px;
}

%upercase {
  text-transform: uppercase;
  font-weight: 700;
  font-size: 12px;
  line-height: 11px;
  letter-spacing: 0.04em;
}

%nav-link-text {
  @extend %upercase;

  color: $secondary-color;
  text-decoration: none;

  @include hover(color, $primary-color);
}

%list-null {
  list-style: none;
  margin: 0;
  padding: 0;
}

%button-null {
  border: none;
  padding: 0;
  background-color: transparent;
  cursor: pointer;
}
