%ic {
  font-family: $font-family-icons;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

[class*="_icon-"] {
  &::before {
    @extend %ic;

    display: block;
  }
}

._icon-arrow::before {
  content: "\e900";
}

._icon-cart::before {
  content: "\e901";
}

._icon-close::before {
  content: "\e902";
}

._icon-favourites::before {
  content: "\e903";
}

._icon-home::before {
  content: "\e904";
}

._icon-icon-burger-menu::before {
  content: "\e905";
}

._icon-icon-burger-menu-hover::before {
  content: "\e906";
}

._icon-minus::before {
  content: "\e907";
}

._icon-plus::before {
  content: "\e908";
}

._icon-search::before {
  content: "\e909";
}
