@mixin on-small-mobile {
  @media (max-width: $mobile-small-max-width) {
    @content;
  }
}

@mixin on-mobile {
  @media (max-width: $mobile-max-width) {
    @content;
  }
}

@mixin on-tablet {
  @media (max-width: $tablet-max-width) {
    @content;
  }
}

@mixin on-desktop {
  @media (max-width: $desktop-max-width) {
    @content;
  }
}

@mixin hover($property, $toValue) {
  transition: #{$property} $effect-duration;

  @media (any-hover: hover) {
    &:hover {
      #{$property}: $toValue;
    }
  }
}

@mixin responsive-value($property, $max, $min) {
  $medium: ($max + $min) / 2;

  #{$property}: $max;

  @include on-desktop {
    #{$property}: $medium;
  };

  @include on-tablet {
    #{$property}: $min;
  };
}

@mixin show-on-tablet($tabletDisplay) {
  display: none;

  @include on-tablet {
    display: $tabletDisplay;
  }
}

@mixin hide-on-tablet($tabletDisplay) {
  display: $tabletDisplay;

  @include on-tablet {
    display: none;
  }
}
