.header {
  position: fixed;
  width: 100%;
  z-index: 1;

  display: flex;
  justify-content: space-between;
  align-items: center;
  @include responsive-value(column-gap, 40px, 0);
  background-color: #fff;

  border-bottom: 1px solid $borders-color;

  &__logo {
    display: flex;
    @include responsive-value(padding-inline, 24px, 18px);
  }

  &__logo-img {
    @include responsive-value(height, 24px, 18px);
    @include hover(transform, scale(1.1));
  }

  &__nav-bar {
    flex-grow: 1;
    display: flex;
    justify-content: space-between;
    @include responsive-value(column-gap, 40px, 0);

    &-left {}

    &-right {
      display: flex;
      flex: 1 0 auto;
      justify-content: end
    }
  }

  &__link {
    @extend %centered-flexbox;

    position: relative;
    @include responsive-value(height, 64px, 52px);

    &::after {
      content: '';
      position: absolute;
      bottom: 0;
      height: 3px;
      width: 100%;
      background-color: $primary-color;
      transition: opacity $effect-duration;
      opacity: 0;
    }

    &:hover {
      &::after {
        opacity: 1;
      }
    }

    &--active {
      color: $primary-color;

      &::after {
        opacity: 1;
      }
    }
  }
}

.catalog-menu {
  position: relative;

  &:hover {
    .catalog-menu__nav {
      display: flex;
    }
  }

  &__open-btn {
    @extend %button-null;
    @include responsive-value(width, 64px, 52px);
    box-sizing: content-box;
    border-left: 1px solid $borders-color;
    border-right: 1px solid $borders-color;

    background: center no-repeat url('../../icons/burger-menu.svg');
    @include hover(background-image, url('../../icons/burger-menu-hover.svg'));

    @include show-on-tablet(flex);
  }

  &__nav {
    @include hide-on-tablet(flex);

    @include on-tablet {
      position: absolute;
      width: 200px;
      border: 1px solid $borders-color;
    }
  }

  &__list {
    @extend %list-null;
    @include responsive-value(column-gap, 64px, 0);
    display: flex;

    @include on-tablet {
      flex-direction: column;
      flex-grow: 1;
    }
  }

  &__item {
    &:first-child {
      @include hide-on-tablet(flex);
    }
  }

  &__link {
    @extend %centered-flexbox;
    @extend %nav-link-text;
    width: 100%;
    height: 100%;

    @include on-tablet {
      box-sizing: content-box;
      justify-content: start;
      padding: 0 10px;
    }
  }
}

.search {
  display: flex;
  justify-content: end;

  @include on-tablet {
    &:hover,
    &:focus-within {
      flex: 1 1 auto;

      .search {
        &__form {
          width: 100%;
          opacity: 1;
        }

        &__open-btn {
          display: none;
        }

        &__btn {
          display: inline-flex;
        }
      }
    }
  }

  &__open-btn {
    @extend %button-null;
    @include responsive-value(width, 64px, 52px);
    box-sizing: content-box;
    border-left: 1px solid $borders-color;

    background: center no-repeat url('../../icons/search.svg');

    @include show-on-tablet(flex);
  }

  &__form {
    position: relative;
    width: 320px;
    border-left: 1px solid $borders-color;
    transition: all $effect-duration;

    @include on-tablet {
      width: 0;
      opacity: 0;
    }
  }

  &__input {
    border: none;
    width: 100%;
    height: 100%;
    box-sizing: border-box;
    padding-inline: 24px;

    font-family: inherit;
    font-size: 14px;
    font-weight: 600;
    color: inherit;

    @include hover(background-color, $hover-color);

    &:focus {
      outline: none;
      background-color: $hover-color;

      & + .search__btn {
        display: inline-flex;
        transform: rotate(45deg);
        background-image: url('../../icons/plus.svg');
      }
    }

    &::placeholder {
      color: $icons-color;
    }
  }

  &__btn {
    @extend %button-null;
    position: absolute;
    right: 0;
    @include responsive-value(width, 64px, 52px);
    height: 100%;
    background: center no-repeat url('../../icons/search.svg');

    @include on-tablet {
      display: none;
    }
  }
}

.controls {
  display: flex;

  &__link {
    @include responsive-value(width, 64px, 52px);
    border-left: 1px solid $borders-color;
  }

  &__icon {
    display: flex;
    position: relative;
  }

  &__qty {
    @extend %centered-flexbox;

    position: absolute;
    top: -50%;
    right: -50%;
    width: 14px;
    height: 14px;
    overflow: hidden;

    border-radius: 50%;
    background-color: $accent-red;
    border: 1px solid #fff;
    color: #fff;
    font-weight: 600;
    font-size: 9px;
  }
}
